import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-action-bar';

const ActionBar = ({ children, className, type, ...remainingProps }) => (
  <div
    className={classnames('row', CSS_PREFIX, {
      [`${CSS_PREFIX}--${type}-controls`]: Boolean(type)
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

ActionBar.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Active
   */
  type: oneOf(['active', 'standard'])
};

ActionBar.displayName = 'ActionBar';

export default ActionBar;
