import React, { useState } from 'react';
import ActionBar from '../../action-bar';
import ActionBarSelectedItemsCount from '../../action-bar-selected-items-count';
import ButtonAction from '../../button-action';
import ButtonIcon from '../../button-icon';
import ButtonLabel from '../../button-label';
import ButtonSplit from '../../button-split';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Input from '../../input';
import Row from '../../row';
import Search from '../../search';
import SearchClearButton from '../../search-clear-button';
import SearchIcon from '../../search-icon';
import Typography from '../../typography';

const ActionBarExample = () => {
  const [state, setState] = useState({
    value: ''
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Standard Controls</Typography>
          <Typography>
            Action bar will load actions that the user can
            apply or initiate broadly to the page content loaded.
            These actions allow users to manipulate the items loaded such as sort and filter.
          </Typography>
          <Typography>
            If using icon buttons, include a tooltip
            for additional clarity.
          </Typography>
          <Typography>
            At this point, user has not made any selection
            of items in the grid, table or list.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ActionBar>
            <Col noGutter>
              <ButtonText>Create Alert</ButtonText>
              <ButtonSplit flippable colorScheme="secondary">
                <ButtonLabel>Export</ButtonLabel>
                <ButtonAction><Icon name="arrow-expand" resolution="tiny" /></ButtonAction>
              </ButtonSplit>
            </Col>
            <Col noGutter alignEnd>
              <Search dynamic>
                <Input
                  autoComplete="off"
                  label="Search"
                  name="q"
                  onChange={e => setState({ value: e.target.value })}
                  placeholder="Search"
                  type="search"
                  useNativePlaceholder
                  value={state.value}
                />
                <SearchIcon>
                  <Icon name="search" resolution="low" />
                </SearchIcon>
                {
                  state.value.length
                    ? <SearchClearButton>
                      <ButtonIcon
                        ariaLabel="clear entry"
                        iconType="light-tiny"
                        onClick={() => setState({ value: '' })}
                      >
                        <Icon name="clear" resolution="tiny" />
                      </ButtonIcon>
                    </SearchClearButton>
                    : undefined
                }
              </Search>
              <ButtonSplit flippable colorScheme="secondary">
                <ButtonLabel>Sort</ButtonLabel>
                <ButtonAction><Icon name="arrow-expand" resolution="tiny" /></ButtonAction>
              </ButtonSplit>
            </Col>
          </ActionBar>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Active Controls</Typography>
          <Typography>
            Once the user has made one or more item selections,
            the action bar will contextually change to host controls
            that can be applied. It will also list how many active items.
          </Typography>
          <Typography>
            If using icon buttons, include a tooltip for additional clarity.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ActionBar type="active">
            <Col noGutter>
              <ButtonSplit flippable>
                <ButtonLabel>Add New</ButtonLabel>
                <ButtonAction><Icon name="arrow-expand" resolution="tiny" /></ButtonAction>
              </ButtonSplit>
              <ButtonText colorScheme="secondary">Close</ButtonText>
              <ButtonText colorScheme="secondary">Force Close</ButtonText>
              <ButtonText colorScheme="secondary">Acknowledge</ButtonText>
            </Col>
            <Col noGutter alignEnd>
              <ActionBarSelectedItemsCount>1 selected</ActionBarSelectedItemsCount>
              <Icon name="close" resolution="low" />
            </Col>
          </ActionBar>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Active Controls: Alternate Layout</Typography>
          <Typography>
            Adjust the hierarchy of the controls in the action bar as
            needed and as it relates to the other elements on the page.
          </Typography>
          <Typography>
            Maintain appropriate hierarchy among items in the set as well and use overflow when needed.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ActionBar type="active">
            <Col noGutter>
              <ActionBarSelectedItemsCount>1 selected</ActionBarSelectedItemsCount>
              <ButtonText colorScheme="tertiary">Clear</ButtonText>
            </Col>
            <Col noGutter alignEnd>
              <ButtonText colorScheme="tertiary">Close</ButtonText>
              <ButtonText colorScheme="tertiary">Force Close</ButtonText>
              <ButtonText colorScheme="tertiary">Acknowledge</ButtonText>
              <ButtonIcon ariaLabel="search" iconType="light">
                <Icon name="options" resolution="tiny" />
              </ButtonIcon>
            </Col>
          </ActionBar>
        </Col>
      </Row>
    </Grid>
  );
};

export default ActionBarExample;
