import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-action-bar-selected-items-count';

const ActionBarSelectedItemsCount = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

ActionBarSelectedItemsCount.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ActionBarSelectedItemsCount.displayName = 'ActionBarSelectedItemsCount';

export default ActionBarSelectedItemsCount;
